import React, { lazy } from 'react';
import { Navigate } from 'react-router-dom';
import Loadable from '../layouts/full-layout/loadable/Loadable';

const FullLayout = Loadable(lazy(() => import('../layouts/full-layout/FullLayout')));
const BlankLayout = Loadable(lazy(() => import('../layouts/blank-layout/BlankLayout')));
const Login = Loadable(lazy(() => import('../views/authentication/Login')));
const DashboardTables = Loadable(lazy(() => import('../views/dashboards/tables')));
const TableData = Loadable(lazy(() => import('../views/dashboards/tableData')));
const AddData = Loadable(lazy(() => import('../views/dashboards/addData')));
const AddTable = Loadable(lazy(() => import('../views/dashboards/addTable')));
const ThemeBuilder = Loadable(lazy(() => import('../views/dashboards/themeBuilder')));


const Router = [
  {
    path: '/',
    element: <FullLayout />,
    children: [
      { path: '/', element: <Navigate to="/dashboards/tables" /> },
      {
        path: '/dashboards/table/:name',
        exact: true,
        element: <TableData />,
      },
      { path: '/dashboards/tables', exact: true, element: <DashboardTables /> },
      { path: '/dashboards/builder', exact: true, element: <ThemeBuilder /> },
      { path: '/dashboards/tables/new', exact: true, element: <AddTable /> },
      { path: '/dashboards/addData', exact: true, element: <AddData /> },
      { path: '*', element: <Navigate to="/dashboards/tables" /> },
    ],
  },
  {
    path: 'auth',
    element: <BlankLayout />,
    children: [
      { path: 'login', element: <Login /> },
      { path: '*', element: <Navigate to="/auth/404" /> },
    ],
  },
];

export default Router;
