import React, { useEffect } from 'react';
import axios from 'axios';
import { BrowserRouter as Router, useRoutes, useNavigate } from 'react-router-dom';
import { ThemeProvider } from '@mui/material';
import { useSelector } from 'react-redux';
import RTL from './layouts/full-layout/customizer/RTL';
import ThemeSettings from './layouts/full-layout/customizer/ThemeSettings';
import RouterConfig from './routes/Router'; // Renamed to avoid conflict
import 'react-perfect-scrollbar/dist/css/styles.css';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

const AppRoutes = () => {
  const routing = useRoutes(RouterConfig);
  const theme = ThemeSettings();
  const customizer = useSelector((state) => state.CustomizerReducer);
  const navigate = useNavigate();

  useEffect(() => {
    axios.interceptors.request.use(
      async function (config) {
        try {
          const authStr = localStorage.getItem('USER_DETAILS');
          if (authStr) {
            const authObj = JSON.parse(authStr);
            config.headers.Authorization = `Bearer ${authObj.access_token}`;
          }
          return config;
        } catch (err) {
          return config;
        }
      },
      function (error) {
        return Promise.reject(error);
      },
    );

    axios.interceptors.response.use(
      function (response) {
        return response;
      },
      function (error) {
        if (error.response && error.response.status === 401) {
          localStorage.removeItem('USER_DETAILS');
          navigate('/auth/login');
        }
        return Promise.reject(error);
      },
    );
  }, [navigate]);

  return (
    <ThemeProvider theme={theme}>
      <RTL direction={customizer.activeDir}>{routing}</RTL>
    </ThemeProvider>
  );
};

const App = () => (
  <Router>
    <AppRoutes />
  </Router>
);

export default App;
